"use client";

import { useState, useEffect } from "react";
import { useParams, useRouter } from "next/navigation";
import { api } from "~/utils/api";
import { toast } from "react-hot-toast";
import NavigationBar from '../app/NavigationBar/';

export default function EditStudentPage() {
  const params = useParams();
  const router = useRouter();
  
  // Get the student ID from URL params
  const id = Array.isArray(params.id) ? params.id[0] : params.id;

  // Fetch student data
  const { data: studentData, isLoading: isLoadingStudent } = api.student.getById.useQuery(
    { id: id as string },
    { enabled: !!id }
  );

  // Mutation for updating student
  const updateStudent = api.student.update.useMutation({
    onSuccess: () => {
      toast.success("Student information updated successfully!");
      router.push('/students'); // Redirect after successful update
    },
    onError: (error) => {
      toast.error("Failed to update student information");
      console.error("Update error:", error);
    }
  });

  // Mutations for additional actions
  const addSubject = api.student.addSubject.useMutation({
    onError: (error) => {
      toast.error("Failed to add subject");
      console.error("Add subject error:", error);
    }
  });

  const addGrade = api.student.addGrade.useMutation({
    onError: (error) => {
      toast.error("Failed to add grade");
      console.error("Add grade error:", error);
    }
  });

  const updateAttendance = api.student.updateAttendance.useMutation({
    onError: (error) => {
      toast.error("Failed to update attendance");
      console.error("Update attendance error:", error);
    }
  });

  const addBehavioralNote = api.student.addBehavioralNote.useMutation({
    onError: (error) => {
      toast.error("Failed to add behavioral note");
      console.error("Add behavioral note error:", error);
    }
  });

  // State for form data
  const [formData, setFormData] = useState({
    name: "",
    studentId: "",
    grade: "",
  });

  // Subjects state
  const [subjects, setSubjects] = useState<Array<{
    id?: string;
    name: string;
    performance?: string;
    grades: Array<{ value: number }>;
  }>>([]);

  // Attendance state
  const [attendance, setAttendance] = useState({
    present: 0,
    absent: 0,
    late: 0
  });

  // Behavioral notes state
  const [behavioralNote, setBehavioralNote] = useState("");

  // Populate form when student data is loaded
  useEffect(() => {
    if (studentData) {
      setFormData({
        name: studentData.name,
        studentId: studentData.studentId,
        grade: studentData.grade,
      });

      setSubjects(studentData.subjects?.map(subject => ({
        id: subject.id,
        name: subject.name,
        performance: subject.performance || undefined,
        grades: subject.grades.map(grade => ({ value: grade.value }))
      })) || []);

      setAttendance(studentData.attendance || {
        present: 0,
        absent: 0,
        late: 0
      });

      // Get the latest behavioral note
      if (studentData.behavioralNotes && studentData.behavioralNotes.length > 0) {
        setBehavioralNote(studentData.behavioralNotes[studentData.behavioralNotes.length - 1].content);
      }
    }
  }, [studentData]);

  // Handle main form input changes
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  // Submit form
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!id) {
      toast.error("No student ID provided");
      return;
    }

    try {
      // Update student basic info
      await updateStudent.mutateAsync({
        id: id as string,
        ...formData
      });

      // Update attendance
      await updateAttendance.mutateAsync({
        studentId: id as string,
        ...attendance
      });

      // Add/update subjects
      for (const subject of subjects) {
        // Add or update subject
        const savedSubject = subject.id 
          ? subject 
          : await addSubject.mutateAsync({
              studentId: id as string,
              name: subject.name,
              performance: subject.performance
            });

        // Add grades for the subject
        for (const grade of subject.grades) {
          await addGrade.mutateAsync({
            subjectId: savedSubject.id,
            value: grade.value
          });
        }
      }

      // Add behavioral note if not empty
      if (behavioralNote.trim()) {
        await addBehavioralNote.mutateAsync({
          studentId: id as string,
          content: behavioralNote
        });
      }

      toast.success("Student information updated successfully!");
      router.push('/students');
    } catch (error) {
      toast.error("Failed to update student information");
      console.error(error);
    }
  };

  if (isLoadingStudent) {
    return <div>Loading...</div>;
  }

  return (
    <div className="p-4 max-w-2xl mx-auto">
      <NavigationBar />
      <h1 className="text-2xl font-bold mb-6 text-center">Edit Student Information</h1>
      
      <form onSubmit={handleSubmit} className="space-y-6">
        {/* Rest of the form remains the same as in the previous version */}
        {/* ... (previous JSX content) ... */}
      </form>
    </div>
  );
}